const myTheme = createTheme({

})
const IndexPage = ({ data, location }) => {

    const params = new URLSearchParams(location.search);
    const o = params.get("o");
    const e = params.get("e");

    useEffect(() => {
        if (o && e) {
            Parse.Cloud.run('verifyEmailAddress', {
                email: e,
                otp: o
            })
        }
    }, [o, e])

    const { isAuth, env } = useAuth()
    const [post, setPosts] = useState([])
    const [preview, setPreview] = useState('')
    const [number, setNumber] = useState('')
    const [day, setDate] = useState('')
    const [month, setMonth] = useState('')
    const [year, setYear] = useState('')
    const [elements, setElements] = useState(null)
    const [accordion, setAccordion] = useState([])
    const theme = useTheme();
    const [numberInfo, setNumberInfo] = useState()
    const isSM = useMediaQuery(theme.breakpoints.up('sm'));
    const [list, setList] = useState()

    const refreshData = async () => {
        const result = await Parse.Cloud.run('getPost', {
            category: `public-${env}`,
        })
        setPosts(result?.map((e) => e.toJSON()) || [])
    }

    useEffect(async () => {
        refreshData()
    }, [])



    var refdiv = React.useRef(null);
    var rte;

    const handleDelete = async (post) => {
        await Parse.Cloud.run('deleteItems', {
            ids: [post.objectId]
        })
        refreshData()
    }

    const handlePost = async (post) => {
        console.log(post)
        await Parse.Cloud.run('post', {
            id: post.itemId,
            title: post.title,
            content: post.content,
            category: `public-${env}`,
            ...post,
        })
        refreshData()
    }

    return (
        <>
            <PostListContainer data={data?.allPost?.edges?.map(e => ({ ...e.node })) || []} />
        </>)
}

export default IndexPage

export const query = graphql`
query MyQuery {
  allPost {
    edges {
      node {
      itemId
      id
      data {
        content      
        title
        video
        colorText {
          bgColor
          content
        }
      }
        createdAt
      }
    }
  }
}

`